import * as React from 'react';
import TextField from '@mui/material/TextField';
import './profile-photographer-page.css'
import {DataGrid, GridColDef, GridRowsProp} from "@mui/x-data-grid";

const columns: GridColDef[] = [
    {field: 'id', headerName: 'ID', width: 40},
    {field: 'event', headerName: '', width: 100},
    {field: 'importance', headerName: '', width: 100},
    {field: 'evaluation', headerName: ' ', width: 100},
];

const rows: GridRowsProp = [
    {id: 1, event: '', importance: '', evaluation: '100',},
    {id: 2, event: '', importance: '', evaluation: '100',},
    {id: 3, event: '', importance: '', evaluation: '100',},
];

export default function ProfilePhotographerPage() {
    return (
        <>
            <div className={'wrapper_profile'}>
                <div>
                    <div>
                        <img
                            src='https://cdn.fishki.net/upload/post/2021/02/16/3613203/dc3783e0069bb6e3b35750bc62606fe3.jpg'
                            style={{width: 200, height: 200, borderRadius: 5}}/>
                    </div>
                    <div className={'profile_name'}>
                        <TextField required id="outlined-required" label="" defaultValue=""/>
                        <TextField required id="outlined-required" label="" defaultValue=""/>
                        <TextField id="outlined-required" label="" defaultValue=""/>
                    </div>
                    <div>
                        <TextField
                            id="outlined-multiline-static"
                            label=" "
                            multiline
                            rows={6}
                            defaultValue="Default Value"
                        />
                    </div>
                </div>
                <div>
                    <div>
                        <TextField required id="outlined-required" label=""/>
                    </div>
                    <div>
                        <p>
                             : 30.11.2023
                        </p>
                    </div>
                    <div>
                        <p>
                            :
                        </p>
                        <p>
                            +79999999999
                        </p>
                        <p>
                            @asdasd
                        </p>
                        <p>
                            @asdasd
                        </p>
                        <p>
                            pochta@pochta.ru
                        </p>
                    </div>
                    <div>
                        <p>
                            : 
                        </p>
                    </div>
                    <div>
                        <button> </button>
                    </div>
                </div>
            </div>
            <div>
                <DataGrid
                    rows={rows}
                    columns={columns}
                    initialState={{
                        pagination: {
                            paginationModel: {page: 0, pageSize: 5},
                        },
                    }}
                    pageSizeOptions={[5, 10]}
                    checkboxSelection
                />
            </div>
        </>
    );
}
